import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, Platform, AlertController } from '@ionic/angular';
import { PostProvider } from '../../providers/post-provider';
import { Storage } from '@ionic/storage';
import { LocalNotifications, ELocalNotificationTriggerUnit } from '@ionic-native/local-notifications/ngx';
//import { Router,  } from '@angular/router';


@Component({
  selector: 'app-futbol-local',
  templateUrl: './futbol-local.page.html',
  styleUrls: ['./futbol-local.page.scss'],
})
export class FutbolLocalPage implements OnInit {

  scheduled = [];
  name_customer: string;
  desc_customer: string;
  contact_customer: string;
  customer = false;


  customers: any = [];
  locals: any = [];
  upsaldos: any = [];
  limit: number = 10;
  start: number = 0;
  phone_number: string;
  username: string;
  user_id: any;
  identificacion; any;
  name_destinatario: any;
  ident_destinatario: any;
  saldo: string;
  login_id: any;
  anggota: any;

  id_local_fut: any;
  imagen: any;
  equipo_uno: any;
  marcador_1: any;
  equipo_dos: any;
  marcador_2: any;
  fecha_inicio: any;
  fecha_final: any;
  estado: any;

  id: number;

    constructor(  
      private plt: Platform,
      private localNotifications: LocalNotifications,
      private lalertCtrl: AlertController,
    private router: Router,
    private postPvdr: PostProvider,
    private storage: Storage,
    private actRoute: ActivatedRoute
  ) {

    this.plt.ready().then(() => {
      this.localNotifications.on('click').subscribe(res => {
        console.log('click: ', res);
        let msg = res.data ? res.data.mydata : '';
        this.showAlert(res.title, res.text, msg);    
      });

      this.localNotifications.on('trigger').subscribe(res => {
        console.log('trigger: ', res);
        let msg = res.data ? res.data.mydata : '';
        this.showAlert(res.title, res.text, msg);
      });
    });

    setTimeout(() => {
      this.customer = true;
    }, 3000);
   }
   doRefresh(event) {
    setTimeout(() => {
      this.ionViewWillEnter();
      event.target.complete();
    }, 1500);
  }



  ionViewWillEnter() {
    this.customers = [];
    this.locals = [];
    this.upsaldos = [];    
    this.start = 0;
    this.loadSaldo();
   this.loadRetos();
    this.storage.get('session_storage').then((res) => {
      this.anggota = res;
      this.login_id = this.anggota.login_id;
      this.user_id = this.anggota.user_id;
      this.identificacion = this.anggota.identificacion;
      //this.saldo = this.anggota.saldo;
      this.phone_number = this.anggota.phone_number;
      this.username = this.anggota.username;
    });
  }




   ngOnInit() {
    this.actRoute.params.subscribe((data: any) =>{
    this.id = data.user_id;
    this.name_customer = data.name;
    this.desc_customer = data.desc;
    this.contact_customer = data.contact;
    this.name_destinatario = data.namedest;
    this.ident_destinatario = data.ident;
    this.id_local_fut = data.local_fut;
    this.imagen = data.img;
    this.equipo_uno = data.uno;
    this.marcador_1 = data.marc1;
    this.equipo_dos = data.dos;
    this.marcador_2 = data.marc2;
    this.fecha_inicio = data.inicio;
    this.fecha_final = data.final;
    this.estado = data.esta;

        console.log(data);

    });
  }

  loadData(event: any) {
    this.start += this.limit;
    setTimeout(() => {
    /* this.loadRetos().then(() => {
    event.target.complete();
    }); */
    },1500);
  }

  loadSaldo() {
  
    return new Promise(resolve => {
      let body = {
        aksi: 'getdata3',            
        saldo : this.saldo,
        user_id :this.user_id,
        login_id: this.login_id
       
      };
      //console.log(this.login_id);

      this.postPvdr.postData(body, 'file_aksi.php').subscribe(data => {
        if(data.result) {
          for(let upsaldo of data.result) {
           this.upsaldos.push(upsaldo)
          
           
        }
        }
        console.log(this.saldo);
        resolve(true);
      });
     
    });
    
  }

  loadRetos() {
  
    return new Promise(resolve => {
      let body = {
        aksi: '894773822',          
       
        equipo_uno : this.equipo_uno,
        user_id :this.user_id,
        login_id : this.login_id,
        contact_customer : this.contact_customer
       
      };
      //console.log(this.login_id);

      this.postPvdr.postData(body, 'file_aksi.php').subscribe(data => {
        if(data.result) {
          for(let local of data.result) {
           this.locals.push(local)
          
           
        }
        }
        console.log(this.saldo);
        resolve(true);
      });
     
    });
    
  }

  scheduleNotification(){
    this.localNotifications.schedule({
      id: 1,
      title: 'HOLA '+ this.username,
      text: 'No tienes nada por ahora',
      data: { mydata: 'Este es el archivo de mi mensaje' },
      trigger: { in: 5, unit: ELocalNotificationTriggerUnit.SECOND },
      //foreground: true
    });

  }

recurringNotification(){
this.localNotifications.schedule({
      id: 22,
      title: 'HOLA '+ this.username,
      text: 'Revisa las novedades que tenemos para ti',
      trigger: { every: ELocalNotificationTriggerUnit.HOUR},
      //foreground: true
    });
}

/* repeatingDaily(){
  this.localNotifications.schedule({
    id: 42,
    title: 'Buenos Dias',
    text: 'Tal vez Quieras revisar tus envios de Contactos',
    trigger:{ every: { hour: 8, minute: 0  }},
    //foreground: true
  });
} */

getHall(){
this.localNotifications.getAll().then(res => { 
  this.scheduled = res;
});
}

showAlert(header, sub, msg){
  this.lalertCtrl.create({
    header: header,
    subHeader: sub,
    message: msg,
    buttons: ['OK']

  }).then(alert => alert.present());
  

}

}
